body {
  background-color: #f0f4f8; /* Przyjemny, jasnoszary kolor */
  color: #333; /* Kolor tekstu */
  margin: 0;
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Styl dla wszystkich przycisk√≥w */
button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4CAF50; /* Zielony kolor */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049; /* Lekko ciemniejszy zielony przy najechaniu */
}
